:root {
	/* Typography  */
	--special-accent-font: "Varela", sans-serif;
	--default-font: "Oxygen", sans-serif;
	/* Colors  */
	--color-primary-accent: #feca57;
	--color-dark-accent: #211c18;
	--color-light-text: #f5f6fa;
	--color-background-light: #f5f6fa;
	--color-dark-text: #211c18;
	/* Easing */
	--transition-out: all 0.2s ease-out;
	--transition-in: all 0.2s ease-in;
	/* Breakpoints  */
	--breakpoint-small: 768px;
}

html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
	font-family: var(--default-font);
	color: var(--color-dark-text);
}

body,
ul {
	margin: 0;
	padding: 0;
}

h1 {
	margin: 0;
}

body {
	background: var(--color-background-light);
}

a {
	text-decoration: none;
	color: var(--color-dark-text);
}

a:hover {
	text-decoration: line-through;
}

button {
	position: relative;
	border: 1px solid var(--color-dark-accent);
	padding: 15px 20px;
	overflow: hidden;
	z-index: 10;
}

button:before {
	position: absolute;
	z-index: -1;
	content: "";
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: var(--color-dark-accent);
	opacity: 0;
	transition: var(--transition-out);
}

button:hover {
	color: var(--color-light-text);
}

button:hover::before {
	opacity: 1;
	transition: var(--transition-in);
}

header {
	position: fixed;
	background: var(--color-background-light);
	z-index: 999;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 15px 20px;
	border-bottom: 2px solid var(--color-dark-accent);
}

nav {
	display: flex;
	place-items: center;
}

nav a {
	margin-left: 15px;
}

input {
	border: 1px solid var(--color-primary-accent);
	background: var(--color-light-text);
}

input::placeholder {
	color: var(--color-dark-text);
}

.full-height {
	height: 100vh;
	width: 100%;
	position: relative;
}

.content-centered {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

/* Hero  */
#hero {
	background: var(--color-background-light);
}

.hero-text .text-element {
	transform: scale(0);
	animation: pulse 2s ease infinite;
	color: var(--color-dark-text);
	font-size: 10vw;
	transform-origin: bottom;
	text-transform: uppercase;
	letter-spacing: 0.05em;
}

@keyframes pulse {
	0% {
		transform: scaleY(0);
	}

	70% {
		transform: scaleY(1);
	}

	100% {
		transform: scaleY(0);
	}
}

/* Toggle Playground  */
#playground {
	background: var(--color-primary-accent);
	position: relative;
}

#playground .buttons {
	position: absolute;
	bottom: 50px;
	right: 50px;
	display: flex;
	justify-content: space-between;
}

.input-container {
	position: absolute;
	top: 100px;
	left: 20px;
}

.input-container input {
	text-align: center;
	height: 50px;
	margin-bottom: 5px;
}

.heading-input {
	width: 200px;
}

.subhead-input {
	width: 350px;
}

.add-btn {
	border-right: none;
}

.letter {
	opacity: 0;
	color: var(--color-dark-accent);
	transform: translateX(10px);
	transition: var(--transition-out);
	font-size: 10vw;
	font-family: var(--special-accent-font);
}

.by-letter.active .letter {
	opacity: 1;
	transform: translateX(0px);
	transition: var(--transition-in);
}

.by-word {
	max-width: 900px;
	text-align: center;
	line-height: 1.5;
	margin: 0;
}

.word {
	color: var(--color-light-text);
	font-size: 1.4rem;
	font-weight: 300;
	opacity: 0;
	transform: translateY(10px);
	transition: var(--transition-out);
}

.by-word.active .word {
	opacity: 1;
	transform: translateY(0px);
	transition: var(--transition-in);
}

@media (min-width: 768px) {
	.input-container {
		top: 200px;
		left: calc(50% - 255px);
		width: 510px;
		display: flex;
		justify-content: space-between;
	}

	.input-container input {
		margin: 0;
		font-size: 1rem;
	}

	.by-word .word {
		font-size: 4rem;
	}
}
